* {
  box-sizing: border-box; }

body {
  margin: 0;
  display: grid;
  grid-template-areas: 'header header header header header header' 'menu main main main main main ' 'menu footer footer footer footer footer';
  background: #4E4D4D; }

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0; }

ul, ol {
  margin: 2px 10px 2px 10px;
  padding: 0;
  list-style-position: inside; }

li {
  margin: 0; }

p {
  margin: 0;
  padding: 0; }

img {
  display: block;
  max-width: 100%;
  height: auto; }

.header {
  grid-area: header;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #00B9FF;
  height: 50px;
  padding: 15px; }
  .header h1 {
    display: none; }
  .header #datoTid {
    display: none; }

.dashboardMenu {
  grid-area: menu;
  display: none; }

.main {
  grid-area: main;
  height: 90vh; }

.dashboard-tile {
  flex-wrap: wrap;
  background: aliceblue;
  margin: 30px;
  padding: 15px; }

#last-hour {
  display: flex; }

#salesChart {
  position: relative;
  height: auto !important;
  width: 70vw !important; }

#newest-orders {
  display: flex; }
  #newest-orders strong {
    width: 100%; }

.footer {
  grid-area: footer;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 50px;
  background: #00B9FF; }

@media screen and (min-width: 1200px) {
  .header {
    justify-content: space-evenly; }
    .header h1 {
      display: block; }
    .header #datoTid {
      display: block; } }
